.container{
    display: flex;
    max-width: 1000px;
    margin: 0 auto;
    margin-top: -10px;
    align-items: end;
    gap: 40px;
}
.top_btn{
    display: flex;
    gap: 32px;
}
.contact_form{
    display: flex;
    flex-direction: column;
    gap: 24px;
    
}
.form_control{
   display: flex; 
   flex-direction: column ;
   width: 100%;
   position: relative;
}
.form_control input{
    height: 40px;
    padding: 0 8px;
}
.form_control label{
    position: absolute;
    top: -12px;
    left: 10px;
    background-color: white;
    padding: 0 10px;
    font-size: 14px;
}
.form_control textarea{
    padding: 8px;
}

.contact_form form{
   display: flex;
   flex-direction: column;
   gap: 20px;
   padding-bottom: 15px;
}